    box-sizing: border-box;
    --blue-main: #4285F4;
    --red-main: #EA4335;
    --yellow-main: #FBBC04;
    --green-main: #34A853;
    --white-light: #ffffff;
    --blue-light: #b7d4ff;
    --yellow-light: #ffecb3;
    --red-light: #ffc4c0;

    --text-blue: #285397;
    --text-grey: rgb(58, 58, 58);

    --button-white: #ffffff;

    @media (prefers-color-scheme: dark) {
        --blue-main: #4285F4;
        --red-main: #EA4335;
        --yellow-main: #FBBC04;
        --green-main: #34A853;
        
        --white-light: #2a2a2a;
        --blue-light: #325b8d; 
        --yellow-light: #927531;
        --red-light: #973936;  
    
        --text-blue: #eef5ff;  
        --text-grey: rgb(240, 240, 240);
    }

    --minor-gap: 10px;
    --major-gap: 20px;
    --double-gap: 40px;

}

@font-face {
    font-family: 'allerbold';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'allerregular';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
    font-weight: normal;
    font-style: normal;
}


body {

    font-family: 'allerregular';

    margin: 0;
    padding: 0;

    height: 100vh;
    width: 100%;

    display: flex;


    button {
        padding: var(--minor-gap);
        font-family: 'allerregular';
        font-size: large;
        color: var(--button-white);
        background-color: var(--blue-main);
        border: none;
        border-radius: 8px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        gap: var(--minor-gap);

        &:hover,
        &:focus {
            cursor: pointer;
            outline: 2px solid var(--text-blue);
            outline-offset: 2px;

        }
    }
    
    sidebar {

        min-width: 260px;
        max-width: 260px;
        overflow: auto;
        scrollbar-width: thin;
        
        background-color: var(--blue-main);
        padding: var(--major-gap);

        display: flex;
        flex-direction: column;
        gap: 20px;            
        position: sticky;
        top: var(--major-gap);

        h1 {
            margin: 0;
            margin-bottom: var(--major-gap);
            color: var(--text-blue);

            &:hover {
                cursor: pointer;
                color: var(--blue-main);
            }
        }

        button {
            padding: 8px 10px;
            font-family: 'allerregular';
            font-size: large;
            background-color: var(--blue-main);
            border: none;
            border-radius: 8px;
        }

        .sidebar-item {
            border-radius: 12px;

            padding: 18px;

            background-color: var(--blue-light);

            
            &.upcoming {
                
                display: flex;
                gap: var(--major-gap);
                
                img {
                    width: 100%;
                }
                
                h1 {
                    margin: 0;
                }
                
                &:hover {
                    cursor: pointer;
                    h1 {
                        color: var(--blue-main);
                    }
                }
            }

            ul {

                list-style: none;

                color: var(--text-grey);
                
                display: grid;
                gap: var(--minor-gap);

                margin: 0;
                margin-top: var(--major-gap);

                padding: 0;

                max-height: 200px;
                overflow-y: scroll;
                scrollbar-width: none;

                
                li {
                    text-wrap: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    padding-left: 0;
                    padding: var(--minor-gap); 
                    border-radius: var(--minor-gap);
                    background-color: var(--white-light);

                    &:hover {
                        cursor: pointer;
                        color: var(--blue-main);
                    }
                }

            }
        }

        
    }


    container {
        width: calc(100vw - 250px);
        min-width: 800px;
        display: grid;
        grid-template-rows: 120px 1fr;

        position: relative;

        background-color: var(--white-light);

        header {

            grid-row: 1;

            display: flex;
            align-items: center;
            
            font-size: 32px;
            color: var(--text-blue);
            
            padding: var(--minor-gap) var(--major-gap);
            margin: var(--major-gap);
            
            border-bottom: 12px solid var(--blue-main);
            border-radius: 12px;

            background-color: var(--blue-light);

            position: sticky;
            top: 0;
            
        }

        content {

            grid-row: 2;
            padding: var(--major-gap) var(--double-gap);

            overflow: auto;
            color: var(--text-grey);

            &.all-projects {

                display: grid;
                grid-template-rows: repeat(3, 300px);
                grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
                gap: var(--major-gap);

            }

            card {
                background-color: var(--blue-light);
                padding: var(--major-gap);
                border: 4px solid var(--blue-main);
                border-radius: 16px;
                
                display: grid;
                grid-template-rows: 24px 1fr 50px;
                grid-template-areas:
                "name"
                "desc"
                "options"
                ;
                
                h3 {
                    color: var(--text-blue);
                    margin: 0;
                }
                
                & .description {
                    overflow: hidden;
                    color: var(--text-grey);
                }

                & .options {
                    display: flex;
                    align-items: center;
                    gap: var(--minor-gap);
                    justify-content: right;

                    & #delete-btn {
                        background-color: var(--red-main);
                    }
                }

                &:hover,
                &:focus {
                    cursor: pointer;
                    box-shadow: 0 0 8px var(--text-blue);
                }

            }

            ul {
                padding: 0;
            }


            li.task-bullet{
                list-style: none;
                max-height: 80px;
                color: var(--text-blue);

                display: grid;
                grid-template: 20px 1fr / 80px 1fr 200px 50px;
                grid-template-areas:
                "options title info edit"
                "options desc info edit"
                ;

                gap: var(--minor-gap);
                column-gap: var(--major-gap);

                background-color: var(--blue-light);

                margin-bottom: var(--major-gap);
                padding: var(--minor-gap) var(--major-gap);

                border-left: 12px solid var(--blue-main);
                border-radius: 12px;

                overflow: hidden;

                &:hover,
                &:focus {
                    cursor: pointer;
                    box-shadow: 0 0 8px var(--text-blue);
                }

                h3 {
                    grid-area: title;
                    margin: 0;
                    text-wrap: nowrap;
                    text-overflow: ellipsis;
                }

                & > p {
                    grid-area: desc;
                    width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    color: var(--text-grey);
                }

                .options {
                    grid-area: options;
                    justify-self: center;
                    align-content: center;

                    .done-btn {
                        background-color: var(--green-main);
                    }
                }

                .info {
                    grid-area: info;
                    display: flex;
                    flex-direction: column;
                    gap: var(--minor-gap);
                    justify-content: center;
                    align-items: flex-start;

                    padding-left: var(--major-gap);

                    .high-priority {
                        color: white;
                        background-color: var(--red-main);
                        border-radius: 6px;
                        padding: 2px var(--minor-gap);
                    }

                    .normal-priority {
                        color: white;
                        background-color: var(--yellow-main);
                        border-radius: 6px;
                        padding: 2px var(--minor-gap);
                    }

                    .low-priority {
                        color: white;
                        background-color: var(--green-main);
                        border-radius: 6px;
                        padding: 2px var(--minor-gap);
                    }

                }

                &.tomorrow {
                    background-color: var(--yellow-light);
                    border-left: 12px solid var(--yellow-main);
                }

                &.today {
                    background-color: var(--red-light);
                    border-left: 12px solid var(--red-main);
                }

                p {
                    margin: 0;
                }

                .edit-btn {
                    grid-area: edit;
                    align-self: center;
                }
            }

        }
    }

    .new-project-dialog[open],
    .edit-project-dialog[open] {

        color: var(--text-grey);
        
        background-color: var(--blue-light);
        height: 400px;
        width: 360px;
        
        &::backdrop {
            backdrop-filter: blur(4px);
        }

        h1 {
            margin: 0;
        }

        display: grid;
        grid-template-rows: 50px 1fr;
        gap: var(--minor-gap);

        border: 4px solid var(--blue-main);
        border-radius: 12px;

        form {
            display: grid;
            grid-template-rows: 50px 1fr 50px;
            gap: var(--minor-gap);

            input,
            textarea {
                border: 2px solid grey;
                border-radius: 8px;
                padding: var(--minor-gap);
                font-family: 'allerregular';
                resize: none;
                
                &:focus,
                &:hover {
                    outline: none;
                    cursor: pointer;
                    border: 2px solid var(--blue-main);
                }

                &[name="name"] {
                    font-size: 20px;
                    
                    &:invalid {
                        outline: 4px solid var(--red-main);
                    }
                }


                &textarea {
                    flex: 1;
                    resize: none;
                }

            }
            
        }
    }

    .edit-task-dialog[open],
    .new-task-dialog[open] {
        background-color: var(--blue-light);
        height: 420px;
        width: 360px;

        color: var(--text-grey);

        &::backdrop {
            backdrop-filter: blur(4px);
        }

        h1 {
            margin: 0;
        }


        display: grid;
        grid-template-rows: 50px 1fr;
        gap: var(--minor-gap);

        border: 4px solid var(--blue-main);
        border-radius: 12px;

        form {
            display: grid;
            grid-template-rows: 50px 1fr 50px 50px 50px;
            grid-template-columns: 1fr 1fr;
            grid-template-areas:
                "name name"
                "desc desc"
                "date priority"
                "project project"
                "submit submit";
            gap: var(--minor-gap);

            * {
                font-family: 'allerregular';
            }

            label {
                font-size: 14px;
                background-color: inherit;
                border: none;
            }
            
            > div {
                display: flex;
                flex-direction: column;
                gap: 2px;
                height: 100%;

                > input,
                > select {
                    height: 30px;
                    border: 2px solid grey;
                    border-radius: 8px;
                    background-color: white;
                    text-align: center;

                    &:focus,
                    &:hover {
                        outline: none;
                        cursor: pointer;
                        border: 2px solid var(--blue-main);
                    }
                }
            }

            input[type="date"] {
                padding: 0;
              }
            
            input[name="name"] {
                grid-area: name;
                border: 2px solid grey;
                font-size: 20px;
                
                &:invalid {
                    outline: 4px solid var(--red-main);
                }

                &:focus,
                &:hover {
                    outline: none;
                    cursor: pointer;
                    border: 2px solid var(--blue-main);
                }
            }


            textarea {
                grid-area: desc;
                flex: 1;
                resize: none;
                border: 2px solid grey;

                &:focus,
                &:hover {
                    outline: none;
                    cursor: pointer;
                    border: 2px solid var(--blue-main);
                }
            }

            div.project {
                grid-area: project;
            }

            button {
                grid-area: submit;
            }


            input,
            textarea {
                border-radius: 8px;
                padding: var(--minor-gap);
            }

        }
    }
}

