--blue-main);\n                    }\n                }\n            }\n\n            ul {\n\n                list-style: none;\n\n                color: var(--text-grey);\n                \n                display: grid;\n                gap: var(--minor-gap);\n\n                margin: 0;\n                margin-top: var(--major-gap);\n\n                padding: 0;\n\n                max-height: 200px;\n                overflow-y: scroll;\n                scrollbar-width: none;\n\n                \n                li {\n                    text-wrap: nowrap;\n                    overflow: hidden;\n                    text-overflow: ellipsis;\n                    padding-left: 0;\n                    padding: var(--minor-gap); \n                    border-radius: var(--minor-gap);\n                    background-color: var(--white-light);\n\n                    &:hover {\n                        cursor: pointer;\n                        color: var(--blue-main);\n                    }\n                }\n\n            }\n        }\n\n        \n    }\n\n\n    container {\n        width: calc(100vw - 250px);\n        min-width: 800px;\n        display: grid;\n        grid-template-rows: 120px 1fr;\n\n        position: relative;\n\n        background-color: var(--white-light);\n\n        header {\n\n            grid-row: 1;\n\n            display: flex;\n            align-items: center;\n            \n            font-size: 32px;\n            color: var(--text-blue);\n            \n            padding: var(--minor-gap) var(--major-gap);\n            margin: var(--major-gap);\n            \n            border-bottom: 12px solid var(--blue-main);\n            border-radius: 12px;\n\n            background-color: var(--blue-light);\n\n            position: sticky;\n            top: 0;\n            \n        }\n\n        content {\n\n            grid-row: 2;\n            padding: var(--major-gap) var(--double-gap);\n\n            overflow: auto;\n            color: var(--text-grey);\n\n            &.all-projects {\n\n                display: grid;\n                grid-template-rows: repeat(3, 300px);\n                grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));\n                gap: var(--major-gap);\n\n            }\n\n            card {\n                background-color: var(--blue-light);\n                padding: var(--major-gap);\n                border: 4px solid var(--blue-main);\n                border-radius: 16px;\n                \n                display: grid;\n                grid-template-rows: 24px 1fr 50px;\n                grid-template-areas:\n                \"name\"\n                \"desc\"\n                \"options\"\n                ;\n                \n                h3 {\n                    color: var(--text-blue);\n                    margin: 0;\n                }\n                \n                & .description {\n                    overflow: hidden;\n                    color: var(--text-grey);\n                }\n\n                & .options {\n                    display: flex;\n                    align-items: center;\n                    gap: var(--minor-gap);\n                    justify-content: right;\n\n                    & #delete-btn {\n                        background-color: var(--red-main);\n                    }\n                }\n\n                &:hover,\n                &:focus {\n                    cursor: pointer;\n                    box-shadow: 0 0 8px var(--text-blue);\n                }\n\n            }\n\n            ul {\n                padding: 0;\n            }\n\n\n            li.task-bullet{\n                list-style: none;\n                max-height: 80px;\n                color: var(--text-blue);\n\n                display: grid;\n                grid-template: 20px 1fr / 80px 1fr 200px 50px;\n                grid-template-areas:\n                \"options title info edit\"\n                \"options desc info edit\"\n                ;\n\n    