       &:focus,\n                &:hover {\n                    outline: none;\n                    cursor: pointer;\n                    border: 2px solid var(--blue-main);\n                }\n\n                &[name=\"name\"] {\n                    font-size: 20px;\n                    \n                    &:invalid {\n                        outline: 4px solid var(--red-main);\n                    }\n                }\n\n\n                &textarea {\n                    flex: 1;\n                    resize: none;\n                }\n\n            }\n            \n        }\n    }\n\n    .edit-task-dialog[open],\n    .new-task-dialog[open] {\n        background-color: var(--blue-light);\n        height: 420px;\n        width: 360px;\n\n        color: var(--text-grey);\n\n        &::backdrop {\n            backdrop-filter: blur(4px);\n        }\n\n        h1 {\n            margin: 0;\n        }\n\n\n        display: grid;\n        grid-template-rows: 50px 1fr;\n        gap: var(--minor-gap);\n\n        border: 4px solid var(--blue-main);\n        border-radius: 12px;\n\n        form {\n            display: grid;\n            grid-template-rows: 50px 1fr 50px 50px 50px;\n            grid-template-columns: 1fr 1fr;\n            grid-template-areas:\n                \"name name\"\n                \"desc desc\"\n                \"date priority\"\n                \"project project\"\n                \"submit submit\";\n            gap: var(--minor-gap);\n\n            * {\n                font-family: 'allerregular';\n            }\n\n            label {\n                font-size: 14px;\n                background-color: inherit;\n                border: none;\n            }\n            \n            > div {\n                display: flex;\n                flex-direction: column;\n                gap: 2px;\n                height: 100%;\n\n                > input,\n                > select {\n                    height: 30px;\n                    border: 2px solid 